.link{
    padding: 30px;
    display: flex;
    justify-content: center;
}
.linkText{
    text-decoration: none;
    background-color: #f1eeee;
    color: rgb(37, 37, 37);
    padding: 15px;
    border-radius: 9px;
}
.linkText:hover{
    background-color: #d4d1d1;
    color: rgb(32, 32, 32);
}